___CSS_LOADER_EXPORT___.push([module.id, `.ai-chat {
  display: flex;
  flex-direction: column;
  flex: 1;
  min-height: 0;
  border-radius: 8px;
  overflow: hidden;
  background-color: #fff;
}

.chat-messages {
  flex: 1;
  overflow-y: auto;
  padding: 15px;
  max-height: calc(100vh - 250px); /* 固定高度，根据视口高度减去输入区域高度 */
}

/* 确保消息内容不会撑开容器 */
.message-content {
  max-width: 80%;
  padding: 10px 15px;
  border-radius: 18px;
  line-height: 1.4;
  white-space: pre-wrap;
  word-wrap: break-word;
  overflow: hidden; /* 防止内容溢出 */
}

/* Markdown 内容区域样式优化 */
.markdown-content {
  line-height: 1.6;
  max-height: 400px; /* 限制单个消息的最大高度 */
  overflow-y: auto; /* 允许内部滚动 */
  padding-right: 5px; /* 为滚动条留出空间 */
}

/* 为 markdown 内容区域添加自定义滚动条样式 */
.markdown-content::-webkit-scrollbar {
  width: 4px;
}

.markdown-content::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.1);
  border-radius: 2px;
}

.markdown-content::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.2);
  border-radius: 2px;
}

.markdown-content::-webkit-scrollbar-thumb:hover {
  background: rgba(0, 0, 0, 0.3);
}

.welcome-message {
  text-align: center;
  color: #666;
  padding: 20px;
  font-style: italic;
}

.message {
  margin-bottom: 15px;
  display: flex;
}

.user-message {
  justify-content: flex-end;
}

.assistant-message {
  justify-content: flex-start;
}

.message-content {
  max-width: 80%;
  padding: 10px 15px;
  border-radius: 18px;
  line-height: 1.4;
  white-space: pre-wrap;
  word-wrap: break-word;
}

.user-message .message-content {
  background-color: #007bff;
  color: white;
  border-bottom-right-radius: 4px;
}

.assistant-message .message-content {
  background-color: #e9ecef;
  color: #333;
  border-bottom-left-radius: 4px;
}

.typing-indicator {
  display: flex;
  align-items: center;
  padding: 10px 15px;
}

.typing-indicator span {
  height: 8px;
  width: 8px;
  background-color: #666;
  border-radius: 50%;
  display: inline-block;
  margin: 0 2px;
  animation: typing 1s infinite;
}

.typing-indicator span:nth-child(2) {
  animation-delay: 0.2s;
}

.typing-indicator span:nth-child(3) {
  animation-delay: 0.4s;
}

@keyframes typing {
 0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-5px);
  }
}

.chat-input-container {
  display: flex;
  flex-direction: column;
  background-color: white;
  padding: 10px;
}

.quick-actions-container {
  padding: 5px;
  border-radius: 8px;
  margin-bottom: 10px;
}

.quick-actions {
  display: flex;
  gap: 8px;
  overflow-x: auto;
  align-items: center;
}

.quick-actions::-webkit-scrollbar {
  height: 4px;
}

.quick-actions::-webkit-scrollbar-thumb {
  background-color: #ccc;
  border-radius: 2px;
}

.quick-action-button {
  padding: 4px 10px;
  background-color: #f0f4f9;
  border: 1px solid #d0d7e0;
  border-radius: 16px;
  color: #333;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.3s ease;
  white-space: nowrap;
  flex-shrink: 0;
  width: auto;
  min-width: fit-content;
  max-width: none;
  margin: 0;
}

.quick-action-button:hover:not(:disabled) {
  background-color: #e1eaf2;
  border-color: #007bff;
  color: #007bff;
  box-shadow: 0 2px 4px rgba(0, 123, 255, 0.2);
}

.quick-action-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  background-color: #f0f0f0;
}

.question-panel {
  padding: 20px;
  border-bottom: 1px solid #eee;
  max-height: 250px;
  overflow-y: auto;
  border-radius: 8px;
  background-color: #f8f9fa;
  margin-bottom: 15px;
}

.question-tabs {
  display: flex;
  border-bottom: 1px solid #e0e0e0;
  margin-bottom: 16px;
  overflow-x: auto;
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

.question-tabs::-webkit-scrollbar {
  display: none; /* Chrome, Safari, and Opera */
}

.tab-button {
  padding: 10px 16px;
  cursor: pointer;
  background-color: transparent;
  border: none;
  border-bottom: 2px solid transparent;
  color: #666;
  font-size: 16px;
  white-space: nowrap;
  transition: color 0.3s, border-color 0.3s;
}

.tab-button.active {
  color: #007bff;
}

.tab-button:hover {
  color: #007bff;
  background-color: transparent;
}

.tab-button.active {
  font-weight: 600;
}

.question-content {
  height: calc(100% - 48px); /* Adjust height based on tabs height */
  overflow-y: auto;
  padding-right: 10px;
}

.question-group {
  margin-bottom: 16px;
}

.category-heading {
  font-size: 14px;
  font-weight: 600;
  color: #333;
  margin-bottom: 12px;
  margin-top: 0;
}

.questions-wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.question-text {
  background-color: #f0f4f9;
  border-radius: 12px;
  padding: 8px 12px;
  font-size: 14px;
  cursor: pointer;
  color: #333;
  line-height: 1.4;
  margin: 0;
  transition: background-color 0.3s;
}

.question-text:hover {
  background-color: #e1eaf2;
}

.chat-input {
  display: flex;
  padding: 0;
  gap: 15px;
  align-items: center;
}

.chat-input input {
  flex: 1;
  padding: 10px 20px;
  border: 1px solid #ddd;
  border-radius: 24px;
  font-size: 16px;
  outline: none;
  transition: all 0.3s ease;
  height: 40px;
  box-sizing: border-box;
}

.chat-input input:focus {
  border-color: #007bff;
  box-shadow: 0 0 0 2px rgba(0, 123, 255, 0.25);
}

.chat-action-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  padding: 0;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  font-size: 20px;
  line-height: 40px;
  transition: all 0.3s ease;
  flex-shrink: 0;
  box-shadow: 0 2px 5px rgba(0, 123, 255, 0.3);
  margin-top: 0;
}

.chat-action-button svg {
  width: 20px;
  height: 20px;
  line-height: 1;
}

.chat-action-button:hover:not(:disabled) {
  background-color: #0056b3;
}

.chat-action-button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

/* Markdown内容样式 */
.markdown-content {
  line-height: 1.5;
}

.markdown-content .markdown-h1 {
  font-size: 1.5em;
  font-weight: 600;
  margin: 0.3em 0 0.2em 0;
  color: #333;
  border-bottom: 1px solid #eaecef;
  padding-bottom: 0.2em;
}

.markdown-content .markdown-h2 {
  font-size: 1.3em;
  font-weight: 600;
  margin: 0.3em 0 0.2em 0;
  color: #333;
}

.markdown-content .markdown-h3 {
  font-size: 1.2em;
  font-weight: 600;
  margin: 0.3em 0 0.2em 0;
  color: #333;
}

.markdown-content .markdown-h4 {
  font-size: 1.1em;
  font-weight: 600;
  margin: 0.3em 0 0.2em 0;
  color: #333;
}

.markdown-content .markdown-h5 {
  font-size: 1em;
  font-weight: 600;
  margin: 0.3em 0 0.2em 0;
  color: #333;
}

.markdown-content .markdown-h6 {
  font-size: 1em;
  font-weight: 600;
  margin: 0.3em 0 0.2em 0;
  color: #666;
}

.markdown-content .markdown-p {
  margin: 0.1em 0;
  line-height: 1.4;
}

.markdown-content .markdown-ul {
  margin: 0.1em 0;
  padding-left: 1.2em;
  list-style-type: disc;
}

.markdown-content .markdown-ol {
  margin: 0.1em 0;
  padding-left: 1.2em;
  list-style-type: decimal;
}

.markdown-content .markdown-li {
  margin: 0.05em 0;
  line-height: 1.3;
}

.markdown-content .markdown-a {
  color: #007bff;
  text-decoration: none;
  border-bottom: 1px dotted transparent;
  transition: border-color 0.3s ease;
}

.markdown-content .markdown-a:hover {
  border-bottom-color: #007bff;
  text-decoration: underline;
}

.markdown-content .markdown-code-inline {
  background-color: rgba(0, 0, 0, 0.06);
  padding: 0.2em 0.4em;
  margin: 0;
  font-size: 0.85em;
  border-radius: 3px;
  font-family: 'SFMono-Regular', Consolas, 'Liberation Mono', Menlo, monospace;
}

.markdown-content .markdown-code-block {
  display: block;
  background-color: rgba(0, 0, 0, 0.06);
  padding: 0.6em;
  margin: 0.3em 0;
  border-radius: 6px;
  overflow-x: auto;
  font-family: 'SFMono-Regular', Consolas, 'Liberation Mono', Menlo, monospace;
  font-size: 0.85em;
  line-height: 1.4;
}

.markdown-content .markdown-pre {
  background-color: rgba(0, 0, 0, 0.06);
  padding: 0.6em;
  margin: 0.3em 0;
  border-radius: 6px;
  overflow-x: auto;
  font-family: 'SFMono-Regular', Consolas, 'Liberation Mono', Menlo, monospace;
  font-size: 0.85em;
  line-height: 1.4;
}

.markdown-content .markdown-blockquote {
  margin: 0.3em 0;
  padding: 0 0.8em;
  color: #6a737d;
  border-left: 0.25em solid #dfe2e5;
}

.markdown-content .markdown-table {
  margin: 0.3em 0;
  border-collapse: collapse;
  width: 100%;
  max-width: 100%;
  overflow: auto;
}

.markdown-content .markdown-thead {
  background-color: rgba(0, 0, 0, 0.03);
}

.markdown-content .markdown-tbody {
  background-color: transparent;
}

.markdown-content .markdown-tr {
  border-top: 1px solid #c6cbd1;
}

.markdown-content .markdown-th {
  padding: 0.5em 0.8em;
  font-weight: 600;
  text-align: left;
  border: 1px solid #dfe2e5;
}

.markdown-content .markdown-td {
  padding: 0.5em 0.8em;
  border: 1px solid #dfe2e5;
}

.markdown-content .markdown-strong {
  font-weight: 600;
  color: #24292e;
}

.markdown-content .markdown-em {
  font-style: italic;
  color: #24292e;
}

.markdown-content .markdown-hr {
  height: 1px;
  padding: 0;
  margin: 0.5em 0;
  background-color: #e1e4e8;
  border: 0;
  opacity: 0.6;
}

@media (max-width: 768px) {
  .ai-chat {
  }
  
  .message-content {
    max-width: 90%;
  }
  
  .markdown-content .markdown-table {
    display: block;
    overflow-x: auto;
    white-space: nowrap;
  }
  
  .markdown-content .markdown-th,
  .markdown-content .markdown-td {
    padding: 0.3em 0.5em;
  }
