ght: 1.4;\n  white-space: pre-wrap;\n  word-wrap: break-word;\n}\n\n.user-message .message-content {\n  background-color: #007bff;\n  color: white;\n  border-bottom-right-radius: 4px;\n}\n\n.assistant-message .message-content {\n  background-color: #e9ecef;\n  color: #333;\n  border-bottom-left-radius: 4px;\n}\n\n.typing-indicator {\n  display: flex;\n  align-items: center;\n  padding: 10px 15px;\n}\n\n.typing-indicator span {\n  height: 8px;\n  width: 8px;\n  background-color: #666;\n  border-radius: 50%;\n  display: inline-block;\n  margin: 0 2px;\n  animation: typing 1s infinite;\n}\n\n.typing-indicator span:nth-child(2) {\n  animation-delay: 0.2s;\n}\n\n.typing-indicator span:nth-child(3) {\n  animation-delay: 0.4s;\n}\n\n@keyframes typing {\n 0%, 100% {\n    transform: translateY(0);\n  }\n  50% {\n    transform: translateY(-5px);\n  }\n}\n\n.chat-input-container {\n  display: flex;\n  flex-direction: column;\n  background-color: white;\n  padding: 10px;\n}\n\n.quick-actions-container {\n  padding: 5px;\n  border-radius: 8px;\n  margin-bottom: 10px;\n}\n\n.quick-actions {\n  display: flex;\n  gap: 8px;\n  overflow-x: auto;\n  align-items: center;\n}\n\n.quick-actions::-webkit-scrollbar {\n  height: 4px;\n}\n\n.quick-actions::-webkit-scrollbar-thumb {\n  background-color: #ccc;\n  border-radius: 2px;\n}\n\n.quick-action-button {\n  padding: 4px 10px;\n  background-color: #f0f4f9;\n  border: 1px solid #d0d7e0;\n  border-radius: 16px;\n  color: #333;\n  font-size: 14px;\n  cursor: pointer;\n  transition: all 0.3s ease;\n  white-space: nowrap;\n  flex-shrink: 0;\n  width: auto;\n  min-width: fit-content;\n  max-width: none;\n  margin: 0;\n}\n\n.quick-action-button:hover:not(:disabled) {\n  background-color: #e1eaf2;\n  border-color: #007bff;\n  color: #007bff;\n  box-shadow: 0 2px 4px rgba(0, 123, 255, 0.2);\n}\n\n.quick-action-button:disabled {\n  opacity: 0.6;\n  cursor: not-allowed;\n  background-color: #f0f0f0;\n}\n\n.question-panel {\n  padding: 20px;\n  border-bottom: 1px solid #eee;\n  max-height: 250px;\n  overflow-y: auto;\n  border-radius: 8px;\n  background-color: #f8f9fa;\n  margin-bottom: 15px;\n}\n\n.question-tabs {\n  display: flex;\n  border-bottom: 1px solid #e0e0e0;\n  margin-bottom: 16px;\n  overflow-x: auto;\n  -ms-overflow-style: none; /* IE and Edge */\n  scrollbar-width: none; /* Firefox */\n}\n\n.question-tabs::-webkit-scrollbar {\n  display: none; /* Chrome, Safari, and Opera */\n}\n\n.tab-button {\n  padding: 10px 16px;\n  cursor: pointer;\n  background-color: transparent;\n  border: none;\n  border-bottom: 2px solid transparent;\n  color: #666;\n  font-size: 16px;\n  white-space: nowrap;\n  transition: color 0.3s, border-color 0.3s;\n}\n\n.tab-button.active {\n  color: #007bff;\n}\n\n.tab-button:hover {\n  color: #007bff;\n  background-color: transparent;\n}\n\n.tab-button.active {\n  font-weight: 600;\n}\n\n.question-content {\n  height: calc(100% - 48px); /* Adjust height based on tabs height */\n  overflow-y: auto;\n  padding-right: 10px;\n}\n\n.question-group {\n  margin-bottom: 16px;\n}\n\n.category-heading {\n  font-size: 14px;\n  font-weight: 600;\n  color: #333;\n  margin-bottom: 12px;\n  margin-top: 0;\n}\n\n.questions-wrapper {\n  display: flex;\n  flex-wrap: wrap;\n  gap: 8px;\n}\n\n.question-text {\n  background-color: #f0f4f9;\n  border-radius: 12px;\n  padding: 8px 12px;\n  font-size: 14px;\n  cursor: pointer;\n  color: #333;\n  line-height: 1.4;\n  margin: 0;\n  transition: background-color 0.3s;\n}\n\n.question-text:hover {\n  background-color: #e1eaf2;\n}\n\n.chat-input {\n  display: flex;\n  padding: 0;\n  gap: 15px;\n  align-items: center;\n}\n\n.chat-input input {\n  flex: 1;\n  padding: 10px 20px;\n  border: 1px solid #ddd;\n  border-radius: 24px;\n  font-size: 16px;\n  outline: none;\n  transition: all 0.3s ease;\n  height: 40px;\n  box-sizing: border-box;\n}\n\n.chat-input input:focus {\n  border-color: #007bff;\n  box-shadow: 0 0 0 2px rgba(0, 123, 255, 0.25);\n}\n\n.chat-action-button {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  width: 40px;\n  height: 40px;\n  padding: 0;\n  background-color: #007bff;\n  color: white;\n  border: none;\n  border-radius: 50%;\n  cursor: pointer;\n  font-size: 20px;\n  line-height: 40px;\n  transition: all 0.3s ease;\n  flex-shrink: 0;\n  box-shadow: 0 2px 5px rgba(0, 123, 255, 0.3);\n  margin-top: 0;\n}\n\n.chat-action-button svg {\n  width: 20px;\n  height: 20px;\n  line-height: 1;\n}\n\n.chat-action-button:hover:not(:disabled) {\n  background-color: #0056b3;\n}\n\n.chat-action-button:disabled {\n  background-color: #ccc;\n  cursor: not-allowed;\n}\n\n/* Markdown内容样式 */\n.markdown-content {\n  line-height: 1.5;\n}\n\n.markdown-content .markdown-h1 {\n  font-size: 1.5em;\n  font-weight: 600;\n  margin: 0.3em 0 0.2em 0;\n  color: #333;\n  border-bottom: 1px solid #eaecef;\n  padding-bottom: 0.2em;\n}\n\n.markdown-content .markdown-h2 {\n  font-size: 1.3em;\n  font-weight: 600;\n  margin: 0.3em 0 0.2em 0;\n  color: #333;\n}\n\n.markdown-content .markdown-h3 {\n  font-size: 1.2em;\n  font-weight: 600;\n  margin: 0.3em 0 0.2em 0;\n  color: #333;\n}\n\n.markdown-content .markdown-h4 {\n  font-size: 1.1em;\n  font-weight: 600;\n  margin: 0.3em 0 0.2em 0;\n  color: #333;\n}\n\n.markdown-content .markdown-h5 {\n  font-size: 1em;\n  font-weight: 600;\n  margin: 0.3em 0 0.2em 0;\n  color: #333;\n}\n\n.markdown-content .markdown-h6 {\n  font-size: 1em;\n  font-weight: 600;\n  margin: 0.3em 0 0.2em 0;\n  color: #666;\n}\n\n.markdown-content .markdown-p {\n  margin: 0.1em 0;\n  line-height: 1.4;\n}\n\n.markdown-content .markdown-ul {\n  margin: 0.1em 0;\n  padding-left: 1.2em;\n  list-style-type: disc;\n}\n\n.markdown-content .markdown-ol {\n  margin: 0.1em 0;\n  padding-left: 1.2em;\n  list-style-type: decimal;\n}\n\n.markdown-content .markdown-li {\n  margin: 0.05em 0;\n  line-height: 1.3;\n}\n\n.markdown-content .markdown-a {\n  color: #007bff;\n  text-decoration: none;\n  border-bottom: 1px dotted transparent;\n  transition: border-color 0.3s ease;\n}\n\n.markdown-content .markdown-a:hover {\n  border-bottom-color: #007bff;\n  text-decoration: underline;\n}\n\n.markdown-content .markdown-code-inline {\n  background-color: rgba(0, 0, 0, 0.06);\n  padding: 0.2em 0.4em;\n  margin: 0;\n  font-size: 0.85em;\n  border-radius: 3px;\n  font-family: 'SFMono-Regular', Consolas, 'Liberation Mono', Menlo, monospace;\n}\n\n.markdown-content .markdown-code-block {\n  display: block;\n  background-color: rgba(0, 0, 0, 0.06);\n  padding: 0.6em;\n  margin: 0.3em 0;\n  border-radius: 6px;\n  overflow-x: auto;\n  font-family: 'SFMono-Regular', Consolas, 'Liberation Mono', Menlo, monospace;\n  font-size: 0.85em;\n  line-height: 1.4;\n}\n\n.markdown-content .markdown-pre {\n  background-color: rgba(0, 0, 0, 0.06);\n  padding: 0.6em;\n  margin: 0.3em 0;\n  border-radius: 6px;\n  overflow-x: auto;\n  font-family: 'SFMono-Regular', Consolas, 'Liberation Mono', Menlo, monospace;\n  font-size: 0.85em;\n  line-height: 1.4;\n}\n\n.markdown-content .markdown-blockquote {\n  margin: 0.3em 0;\n  padding: 0 0.8em;\n  color: #6a737d;\n  border-left: 0.25em solid #dfe2e5;\n}\n\n.markdown-content .markdown-table {\n  margin: 0.3em 0;\n  border-collapse: collapse;\n  width: 100%;\n  max-width: 100%;\n  overflow: auto;\n}\n\n.markdown-content .markdown-thead {\n  background-color: rgba(0, 0, 0, 0.03);\n}\n\n.markdown-content .markdown-tbody {\n  background-color: transparent;\n}\n\n.markdown-content .markdown-tr {\n  border-top: 1px solid #c6cbd1;\n}\n\n.markdown-content .markdown-th {\n  padding: 0.5em 0.8em;\n  font-weight: 600;\n  text-align: left;\n  border: 1px solid #dfe2e5;\n}\n\n.markdown-content .markdown-td {\n  padding: 0.5em 0.8em;\n  border: 1px solid #dfe2e5;\n}\n\n.markdown-content .markdown-strong {\n  font-weight: 600;\n  color: #24292e;\n}\n\n.markdown-content .markdown-em {\n  font-style: italic;\n  color: #24292e;\n}\n\n.markdown-content .markdown-hr {\n  height: 1px;\n  padding: 0;\n  margin: 0.5em 0;\n  background-color: #e1e4e8;\n  border: 0;\n  opacity: 0.6;\n}\n\n@media (max-width: 768px) {\n  .ai-chat {\n  }\n  \n  .message-content {\n    max-width: 90%;\n  }\n  \n  .markdown-content .markdown-table {\n    display: block;\n    overflow-x: auto;\n    white-space: nowrap;\n  }\n  \n  .markdown-content .markdown-th,\n  .markdown-content .markdown-td {\n    padding: 0.3em 0.5em;\n  }\n}"],"sourceRoot":""}]);
